"use strict";
/**
 * 랜덤 배경
 */
const wrapper = document.querySelector("#wrapper");
const bgImages = ["bg-couple-normal.jpeg", "bg-family-normal.jpeg", "bg-winter-normal.jpeg", "bg-woman-normal.jpeg"];
const randomImage = bgImages[Math.floor(Math.random() * 3)];
wrapper.style.background = `url(assets/${randomImage}) no-repeat center center`;
wrapper.style.backgroundSize = "contain";

/**
 * 시계
 */
const clock = document.querySelector(".clock__content");

const viewTime = () => {
  const now = new Date();
  const nowHour = String(now.getHours()).padStart(2, "0");
  const nowMinute = String(now.getMinutes()).padStart(2, "0");
  const nowSecond = String(now.getSeconds()).padStart(2, "0");
  const time = `${nowHour}:${nowMinute}:${nowSecond}`;
  clock.innerText = time;
};

viewTime();
setInterval(viewTime, 1000);

/**
 * 날씨 api
 */
const API_KEY = "b0be9ca452b56cd351eea81bcafa0f44";
const weatherCity = document.querySelector(".weather__city");
const weatherCondition = document.querySelector(".weather__condition");

const getOkay = async (position) => {
  const lat = position.coords.latitude;
  const lon = position.coords.longitude;
  const targetURL = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${API_KEY}&units=metric`;

  // openweather API 요청
  const response = await fetch(targetURL);
  if (response.status === 200) {
    const data = await response.json();
    weatherCity.innerText = data.name;
    weatherCondition.innerText = `${data.weather[0].main} / ${data.main.temp}`;
  } else {
    throw new Error("연결에 실패하였습니다.");
  }
};
const getFail = () => {
  console.log("위치를 가져오는데 실패하였습니다.");
};

navigator.geolocation.getCurrentPosition(getOkay, getFail);

/**
 * login
 */
const greeting = document.querySelector(".login__greeting");
const loginForm = document.querySelector(".login__form");
const HIDDEN_CLASS_NAME = "hidden";
const user_key = "user";
const savedUser = localStorage.getItem(user_key);

const submitLoginHandler = (event) => {
  event.preventDefault();
  const nickname = document.querySelector(".login__nick").value;
  loginForm.classList.add(HIDDEN_CLASS_NAME);
  printNickname(nickname);
  setUserLocalStorage(nickname);
};

const setUserLocalStorage = (nickname) => {
  localStorage.setItem("user", nickname);
};

const printNickname = (nickname) => {
  loginForm.classList.add(HIDDEN_CLASS_NAME);
  document.querySelector(".todo-list").classList.remove("hidden");
  greeting.innerText = `Hello ${nickname}`;
};

savedUser ? printNickname(savedUser) : loginForm.addEventListener("submit", submitLoginHandler);

/**
 * todo List
 */
const todoForm = document.querySelector(".todo-list__form");
const todoInput = document.querySelector(".todo-list__input");
const todoContent = document.querySelector(".todo-list__content");
const TODO_KEY = "todo";
const savedTodos = localStorage.getItem(TODO_KEY);
let todos = [];

const submitTodoFormHandler = (event) => {
  event.preventDefault();
  const todoValue = todoInput.value;
  todoInput.value = "";
  const newTodoObj = {
    text: todoValue,
    id: Date.now(),
  };
  saveTodo(newTodoObj);
  printTodo(newTodoObj);
  console.log(todos);
};

const printTodo = (todo) => {
  const li = document.createElement("li");
  const button = document.createElement("button");
  button.innerText = "X";
  button.addEventListener("click", deleteTodo);
  li.innerText = todo.text;
  li.id = todo.id;
  li.appendChild(button);
  todoContent.appendChild(li);
};

const saveTodo = (todo) => {
  localStorage.setItem(TODO_KEY, JSON.stringify(todos));
};

const deleteTodo = (event) => {
  const li = event.target.parentElement;
  li.remove();
  console.log(li);
  todos = todos.filter((toDo) => toDo.id !== parseInt(li.id));
  saveTodo();
};

if (savedTodos !== null) {
  const parsedTodo = JSON.parse(savedTodos);
  console.log(parsedTodo);
  todos = parsedTodo;
  parsedTodo.forEach(printTodo);
}

todoForm.addEventListener("submit", submitTodoFormHandler);
